@import url('https://fonts.googleapis.com/css?family=Poppins:100,200,300,400,500,600,700,800,900&display=swap');
.fp {
  width: 100%;
  max-width: 1024px;
  display: flex;
  justify-content: space-between;
  gap: 20px;
}

.fpItem{
  flex: 1;
  gap: 10px;
  display: flex;
  flex-direction: column;
}

.fpImg{
  width: 100%;
  height: 250px;
  object-fit: cover;
}

.fpName{
  color: #333;
  font-weight: bold;
}

.fpCity{
  font-weight: 300;
}

.fpPrice{
  font-weight: 500;
}

.fpRating>button{
  background-color: #75FBC0;
  color: #303030;
  border: none;
  padding: 3px;
  margin-right: 10px;
  font-weight: bold;
}

.fpRating>span{
  font-size: 14px;
}

.navButton{
  margin-left: 20px;
  border-radius: 100px;
  border: 1px solid #303030;
  padding: 10px 20px;
  cursor: pointer;
  color: #303030;
  background-color: #75FBC0;
  font-family: 'Poppins', sans-serif;
  font-weight: 'bold';
  font-size: medium;
}

.modalFeature {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  align-self: center;
  padding: 20px;
}

.modalName{
  margin-bottom: 10px;
  font-family: 'Poppins';
}

.modalInfo{
  margin-bottom: 10px;
  font-family: 'Poppins';
  font-size: 15px;
  font-weight: bold;
}

.modalPar{
  font-family: 'Poppins';
  font-size: 16px;
}