.featured{
  width: 100%;
  max-width: 1024px;
  display: flex;
  justify-content: space-between;
  gap: 20px;
  z-index: 1;
  /* background-color:#75FBC0 */

}

.featuredItem{
  position: relative;
  color: white;
  border-radius: 10px;
  overflow: hidden;
  height: 250px;
  flex:1
}

.featuredImg{
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.featuredTitles{
  position: absolute;
  bottom: 20px;
  left: 20px;
}

.navButton{
  margin-left: 20px;
  border-radius: 100px;
  border: 1px solid #303030;
  padding: 10px 20px;
  cursor: pointer;
  color: #303030;
  background-color: #75FBC0;
  font-family: 'Poppins', sans-serif;
  font-weight: 'bold';
  font-size: medium;
}

.modalFeature {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  align-self: center;
  padding: 20px;
}

.modalName{
  margin-bottom: 10px;
  font-family: 'Poppins';
}

.modalInfo{
  margin-bottom: 10px;
  font-family: 'Poppins';
  font-size: 15px;
  font-weight: bold;
}

.modalPar{
  font-family: 'Poppins';
  font-size: 16px;
}